.r-button {
  cursor: pointer;
  &:before {
    content: '';
    pointer-events: none;
    /*
    background: url('../../assets/images/button_bg.svg') no-repeat center;
    width: 220px;
    height: 220px;
    top: -60px;
    left: -70px;
    */
    display: block;
    position: absolute;
    @include button-outside;
  }
  &:after {
    content: '';
    @include button-inside;
  }
  position: relative;
  width: 82px;
  height: 82px;
  background: none;
  border: none;
  &:focus{
    outline: none;
  }
  &--bigger {
    width: 96px;
    height: 96px;
  }
  &[disabled], &.is-disabled {
    &::after {
      background: #12100c;
    }
    &::before {
      background: #12100c !important;
      @include orange-shadow(0.1);
    }
    span {
      opacity: 0.3;
    }
  }

  /*
    &::before {
      background-image: url('../../assets/images/button_bg_disabled.svg');
      width: calc(100% + 76px);
      height: calc(100% + 76px);
      top: -34px;
      left: -37px;
    }
  */

  &.is-mouse-down {
    &::after {
      opacity: 0;
    }
    span {
      @include text-active;
    }
  }
  span {
    z-index: 99;
    font-size: 23px;
    margin: -2px 0 0 -2px;
    @include button-text;
    &:not(.icon) {
      text-transform: uppercase;
      font-size: 25px;
    }
  }
}



