@font-face {
  font-family: 'fontello';
  src: url('../../assets/customFonts/fontello/fontello.eot?769517');
  src: url('../../assets/customFonts/fontello/fontello.eot?769517#iefix') format('embedded-opentype'),
  url('../../assets/customFonts/fontello/fontello.woff2?769517') format('woff2'),
  url('../../assets/customFonts/fontello/fontello.woff?769517') format('woff'),
  url('../../assets/customFonts/fontello/fontello.ttf?769517') format('truetype'),
  url('../../assets/customFonts/fontello/fontello.svg?769517#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}

@function icon-char($charname) {
  @return map-get($fontello-icons, $charname);
}

.icon {
  transition: all 0.2s ease;
  &::before, &::after {
    font-family: 'fontello';
  }
}

@each $property, $value in $fontello-icons {
  .icon--#{$property} {
    &::before {
      content: $value;
    }
  }
}

.icon--spacing {
    &::before {
        margin-right: 10px;
    }
    &::after {
        margin-left: 10px;
    }
}



