@mixin dark-gradient {
  background: $color-black;
  background: -moz-linear-gradient(-45deg, #543b18 0%, #3b2c16 20%, #2d2415 39%, #251f15 50%, #1c1912 75%, #050504 100%);
  background: -webkit-gradient(left top, right bottom, color-stop(0%, #543b18), color-stop(20%, #3b2c16), color-stop(39%, #2d2415), color-stop(50%, #251f15), color-stop(75%, #1c1912), color-stop(100%, #050504));
  background: -webkit-linear-gradient(-45deg, #543b18 0%, #3b2c16 20%, #2d2415 39%, #251f15 50%, #1c1912 75%, #050504 100%);
  background: -o-linear-gradient(-45deg, #543b18 0%, #3b2c16 20%, #2d2415 39%, #251f15 50%, #1c1912 75%, #050504 100%);
  background: -ms-linear-gradient(-45deg, #543b18 0%, #3b2c16 20%, #2d2415 39%, #251f15 50%, #1c1912 75%, #050504 100%);
  background: linear-gradient(135deg, #543b18 0%, #3b2c16 20%, #2d2415 39%, #251f15 50%, #1c1912 75%, #050504 100%);
}

