@mixin button-inside {
  display: block;
  width: calc(100% - 6px);
  height: calc(100% - 6px);
  position: absolute;
  top: 3px;
  left: 3px;
  border-radius: 50%;
  transition: all 0.2s ease;
  opacity: 1;
  @include dark-gradient;
  -webkit-box-shadow: 6px 6px 20px $color-black;
  -moz-box-shadow: 6px 6px 20px $color-black;
  box-shadow: 6px 6px 20px $color-black;
}

@mixin orange-shadow($opacity: 0.25) {
  -webkit-box-shadow: -6px -8px 25px 5px rgba(248,142,31, $opacity);
  -moz-box-shadow: -6px -8px 25px 5px rgba(248,142,31, $opacity);
  box-shadow: -6px -8px 25px 5px rgba(248,142,31, $opacity);
}

@mixin button-outside() {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border-radius: 50%;
  @include orange-gradient;
  transition: all 0.2s ease;
  @include orange-shadow;
}

@mixin button-text {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  text-align: center;
  transform: translateY(-50%);
  transition: all 0.2s ease;
  @include text-inactive;
}


@mixin text-inactive {
  color: #f9a123;
  text-shadow: 1px 1px 0 $color-black;
  transition: all 0.2s ease;
}

@mixin text-active {
  color: $color-black;
  text-shadow: 1px 1px 0 #fcbd63;
}



