body {
  font-family: $font-family;
  color: $color-beige;
  font-size: 100%;
  line-height: 1.5;
  background-color: #1a1711;
  margin: 0;
}
input, textarea, button, select {
  font-family: $font-family;
}
ul {
  margin: 0;
  list-style: none;
  padding: 0;
}

.ul--horizontal {
  li {
    display: inline-block;
  }
}

p {
  margin: 0;
}

a, button, label {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: transparent;
}

h1, h2, h3 {
  font-weight: normal;
  font-family: $font-family;
  margin: 0;
}

.text--lower {
  text-transform: lowercase;
}



