.app-layout-pos--abs--bottom-left {
  position: absolute;
  left: 100px;
  bottom: 100px;
}
.app-pos--abs--center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.app-pos--abs--bottom-left {
  position: absolute;
  left: 0;
  bottom: 0;
}
.app-pos--abs--bottom-right {
  position: absolute;
  right: 0;
  bottom: 0;
}
.app-pos--abs--top-left {
  position: absolute;
  left: 0;
  top: 0;
}
.app-pos--abs--top-right {
  position: absolute;
  right: 0;
  top: 0;
}



